@import "../../utils/commons.scss";

.wrapper {
  width: 100%;
  background: #5797f9;
}

.container {
  width: var(--fixed-width);
  margin: 0 auto;
  padding: 3rem 0;

  > h3 {
    font-size: 1.5rem;
    margin-bottom: 1rem;
    text-align: center;
  }
  h3,
  p {
    color: white;
  }
  .hLine {
    width: 80%;
    height: 2px;
    background: white;
    margin: 2rem auto;
  }
  .content {
    margin-top: 2rem;

    .contentItem {
      @extend .flexRow;
      align-items: flex-start;

      .image {
        @extend .flexRow;
        width: 50px;
        height: 50px;
        border-radius: 50%;
        background: white;
        img {
          width: 100%;
        }
      }

      .text {
        margin-left: 1rem;
      }
    }
  }
}
